import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Box from '@material-ui/core/Box';
import TrelloCard from "./TrelloCard";
import Button from '@material-ui/core/Button';
import AddIcon from '@material-ui/icons/Add';

const TrelloBox = ({ title, cardData }) => {
  const classes = useStyles();
  const [cards, setCards] = useState(cardData.cards);

  const addCard = () => {
    setCards([...cards, {"id": "1", "title": "hehe" }])
  };

  return (
    <Box className={classes.root} bgcolor="#333333">
      <p className={classes.title}>{title}</p>
      {cards && cards.map(item => {
        return (<React.Fragment key={item.id}>
          <TrelloCard title={item.title} />
        </React.Fragment>)
      })}

      <Button
        variant="contained"
        color="primary"
        startIcon={<AddIcon />}
        className={classes.add}
        size="small"
        onClick={addCard}
      >
        Add another card
      </Button>
    </Box>
  );
};

const useStyles = makeStyles({
  root: {
    minWidth: 200,
    maxWidth: 200,
    padding: 10,
    margin: 10,
    borderRadius: 5
  },
  title: {
    margin: 0,
    marginBottom: 5,
    marginLeft: 5,
    fontWeight: "bold",
    fontSize: 12
  },
  add: {
    fontSize: 12,
    textTransform: "none",
    background: "#333333",
    '&:hover': {
      background: "#535353"
    }
  }
});

export default TrelloBox;